import VueFireTest from './VueFireTest'

const hidden = [
  {
    path: '/vuefiretest',
    component: VueFireTest,
    name: 'vuefiretest',
    meta: {
      title: 'VueFire test',
      name: 'vuefiretest',
      hidden: true
    }
  }
]

export default hidden



